document.addEventListener('DOMContentLoaded', () => {
  document.getElementById('numberOfArticlesForm').addEventListener('submit', defineNumberOfFetchedArticles)
})

const defineNumberOfFetchedArticles = (event) => {
  event.preventDefault()

  const noa = document.getElementById('numberOfArticles').value

  fetch(`https://api.spaceflightnewsapi.net/v3/articles?_limit=${noa}`)
    .then(res => res.json())
    .then(completeData => {
      console.log(completeData)

      const data = completeData.map((item) =>
        `
      <div class='article'>
        <div class='article__content'>
          <p>title: ${item.title}</p>
          <p>newsSite: ${item.newsSite}</p>
          <p>publishedAt: ${item.publishedAt}</p>
          <p>summary: ${item.summary}</p>
          <button>
            <a href=${item.url} target="_blank" rel="noopener noreferrer"}>Read article</a>
          </button>
        </div >
        <img class='article__image' src=${item.imageUrl} alt='image'>
        </div>
      `
      )
      document.getElementById("content").innerHTML = data
    }
    )
    .catch(error => console.log(error))
}

fetch('https://api.spaceflightnewsapi.net/v3/articles/count')
  .then(res => res.json())
  .then(count => {
    console.log(count)
  }
  )
  .catch(error => console.log(error))
